<template>
  <div class="page_play"
       :style="{'background-image':`url(${songs.al.picUrl})`}">
    <div class="play_headernav">
      <div class="play_headerleft">
        <van-icon name="https://s1.ax1x.com/2020/03/28/Gk5Bse.png"
                  size="25"
                  @click="$router.go(-1)" />
        <div style="margin-left:20px;width:100%">
          <p>{{songs.name}}</p>
          <p>{{ar}}&gt;</p>
        </div>
      </div>
      <div>
        <van-icon name="https://s1.ax1x.com/2020/03/28/GFs58A.png"
                  size="30" />
      </div>
    </div>
    <!-- 碟 -->
    <div class="plate"
         ref="plate"
         @click.capture="changeToggle">
      <!-- 控制杆 -->
      <div class="plate_bar"
           ref="plate_bar"></div>
      <div class="m-song-disc">
        <div class="m-song-img">
          <div class="m-song_bcg"
               ref="xz"
               id="xz">
            <div class="m_songpicurl">
              <img :src="songs.al.picUrl"
                   alt="">
            </div>
          </div>
        </div>
      </div>
      <!-- 按钮组 -->
      <div class="plate_btn">
        <van-icon name="like-o"
                  size="30"
                  color="#ffffff" />
        <van-icon name="https://s1.ax1x.com/2020/03/28/GFsf4H.png"
                  size="30" />
        <van-icon name="https://s1.ax1x.com/2020/03/29/GV3W38.png"
                  size="30" />
        <van-icon name="https://s1.ax1x.com/2020/03/28/GFsRED.png"
                  :badge="total|changeTotal"
                  size="30" />
        <van-icon name="https://s1.ax1x.com/2020/03/29/GV3jvF.png"
                  size="30" />
      </div>
    </div>
    <!-- 滑块 -->
    <div class="plate_bottom">
      <div class="slide_flex">
        <span>{{currentTime|audioTime}}</span>
        <div class="plate_slider">
          <van-slider v-model="currentTime"
                      @change="changeAudioTime()"
                      active-color="#797979"
                      :max="dtime">
            <template #button>
              <div class="custom-button">
              </div>
            </template>
          </van-slider>
        </div>
        <!-- <span>{{addNum(parseInt(songs.dt/1000/60))+':'+addNum((songs.dt/1000%60).toFixed(0))}}</span> -->
        <span>{{dtime|audioTime}}</span>

      </div>
      <!-- 功能按钮 -->
      <div class="plate_gn">
        <van-icon name="https://s1.ax1x.com/2020/03/29/GVNM1x.png"
                  size="30"
                  color="#ffffff" />
        <van-icon name="https://s1.ax1x.com/2020/03/29/GVYOo9.png"
                  size="30" />
        <!-- <div class="sprite1"
             ref="sprite1"
             @click.stop="changePlay">
        </div> -->
        <van-icon :name="playicon"
                  size="45"
                  @click="changePlay" />
        <van-icon name="https://s1.ax1x.com/2020/03/29/GVtFdH.png"
                  size="30" />
        <van-icon name="https://s1.ax1x.com/2020/03/29/GVtmSP.png"
                  size="30" />
      </div>
    </div>
    <audio :src="songUrl"
           autoplay
           loop
           ref="audio"
           id="audio"
           @loadedmetadata="getAudioLength(1) "
           @timeupdate="audioTimeUpdate()">该浏览器不支持audio</audio>
  </div>
</template>

<script>
export default {
  data () {
    return {
      // value: '0',
      id: '',
      total: '',// 评论数量
      ar: '',
      // 总时长
      dtime: '',
      // 当前播放时长
      currentTime: 0,
      lastTime: null,//标记时间戳
      // 播放状态
      playState: true,
      playicon: 'https://s1.ax1x.com/2020/03/29/GZQtiV.png',
      // 切换碟和歌词
      showtoggle: true,
      // 切换播放状态
      showplay: true,
      songs: {
        al: {
          picUrl: ''
        },
        dt: ''
      },
      songUrl: {}
    }
  },
  filters: {
    // 将时间戳转为分钟 秒数
    audioTime (value) {
      let m = parseInt(value / 60)
      let s = Math.ceil(value % 60)
      if (m < 10)      {
        if (s < 10)        {
          return '0' + m + ':' + "0" + s
        }
        return '0' + m + ':' + s
      } else if (m > 10)      {
        if (s < 10)        {
          return m + ':' + '0' + s
        }
        return m + ':' + s
      }
    },
    // 转换单位
    changeTotal (value) {
      if (value >= 10000)      {
        return '1w+'
      } else if (value >= 999)      {
        return '999+'
      } else      {
        return value
      }
    }
  },
  computed: {

  },
  methods: {
    async fetchSong () {
      const res = await this.$http.get(`song/detail?ids=${this.id}`)
      this.songs = res.data.songs[0]
      this.$set(this.songs, 'dt', res.data.songs[0].dt)
      this.ar = this.songs.ar.map(v => v.name).join('/')
    },
    async fetchSongUrl () {
      const res = await this.$http.get(`song/url?id=${this.id}`)
      this.songUrl = res.data.data[0].url
    },
    // 获取评论数量
    async fetchComments () {
      const res = await this.$http.get(`comment/music?id=${this.id}`)
      this.total = res.data.total
      console.log(this.total);

    },
    // 点击切换碟隐藏显示
    changeToggle () {
      if (this.showtoggle)      {
        this.$refs.plate.style.opacity = 0
        this.showtoggle = !this.showtoggle
      } else      {
        this.$refs.plate.style.opacity = 1
        this.showtoggle = !this.showtoggle
      }
    },
    // 暂停，开始播放
    changePlay () {
      if (this.playState)      {
        const audio1 = document.getElementById('audio')
        audio1.pause();
        this.playState = false
        this.playicon = 'https://s1.ax1x.com/2020/03/28/GAbOgI.png'
        this.$refs.plate_bar.style.transform = 'rotate(-35deg)'
        this.$refs.xz.style.animationPlayState = 'paused'
        this.$refs.plate_bar.style.transformOrigin = '12px 12px'
      } else      {
        const audio1 = document.getElementById('audio')
        // 开始播放
        audio1.play();
        console.log(Math.abs(this.currentTime - this.$refs.audio.cucurrentTime) > 2 ? this.$refs.audio.currentTime = this.currentTime : '');
        // 暂停旋转
        this.$refs.xz.style.animationPlayState = 'running'
        // 改变图标
        this.playicon = 'https://s1.ax1x.com/2020/03/29/GZQtiV.png'
        // 控制杆拉回
        this.$refs.plate_bar.style.transform = 'rotate(0deg)'
        this.$refs.plate_bar.style.transformOrigin = '12px 12px'
        // 播放状态为true
        this.playState = true
      }
    },
    // 判断时长
    addNum (value) {
      if (value < 10)      {
        return '0' + value
      } else      {
        return value
      }
    },
    // 获取audio时长
    getAudioLength (type) {
      if (type == 1)      {
        this.dtime = this.$refs.audio.duration
      }
    },
    // 每一秒更新播放时间
    audioTimeUpdate () {
      let nowTime = Date.now()
      let gapTime = 1000;
      if (!this.lastTime || nowTime - this.lastTime > gapTime)      {
        if (this.$refs.audio)        {
          let time = this.$refs.audio.currentTime
          this.lastTime = time
          this.currentTime = this.lastTime
        }
        this.lastTime = nowTime
      }
    },
    // 滑动后点击重新设置时间
    changeAudioTime (value) {
      if (this.$refs.audio)      {
        console.log(this.$refs.audio.currentTime);
        this.$refs.audio.currentTime = this.currentTime
      }
    }
  },
  created () {
    this.id = this.$route.params.id
    this.fetchSong()
    this.fetchSongUrl()
    this.fetchComments()
  },
}
</script>

<style>
.page_play {
  height: 100vh;
  box-sizing: border-box;
  position: relative;
  background-size: cover;
  background-position: center;
  z-index: 1;
  opacity: 1;
}
.page_play::before {
  content: " ";
  position: absolute;
  box-sizing: border-box;
  left: 0;
  right: 0;
  bottom: 0;
  top: 0;
  z-index: 3;
  background-color: rgba(0, 0, 0, 0.5);
}
.page_play::after {
  content: "";
  width: 100%;
  box-sizing: border-box;
  height: 100vh;
  position: absolute;
  background-size: 120%;
  left: 0;
  right: 0px;
  filter: blur(15px);
  background: inherit;
  z-index: 2;
  opacity: 1;
  top: 0;
}
/* 头部 */
.play_headernav {
  position: relative;
  box-sizing: border-box;
  display: flex;
  align-items: center;
  z-index: 999;
  padding: 10px 10px;
  height: 60px;
  justify-content: space-between;
}

.play_headerleft {
  width: 80%;
  display: flex;
  box-sizing: border-box;
  align-items: center;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.play_headerleft p:nth-child(1) {
  display: inline-block;
  width: 60%;
  box-sizing: border-box;
  margin: 0;
  color: #fff;
  font-size: 16px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.play_headerleft p:nth-child(2) {
  margin: 0;
  color: #fff;
  font-size: 10px;
}
/* 碟 */
.plate {
  box-sizing: border-box;
  position: relative;
  width: 100%;
  height: 450px;
  z-index: 800;
  transition: opacity 0.2s ease-in-out;
}
.plate .plate_bar {
  z-index: 999;
  left: 50%;
  background-image: url(https://s1.ax1x.com/2020/03/29/GZ3zeH.png);
  background-repeat: no-repeat;
  position: absolute;
  width: 96px;
  top: -50px;
  box-sizing: border-box;
  height: 137px;
  transition: transform 1s linear;
}

.m-song-disc {
  width: 296px;
  margin: 0 auto;
  height: 296px;
  box-sizing: border-box;
}
.m-song-img {
  margin-top: 50px;
  width: 100%;
  height: 100%;
  box-sizing: border-box;
}
.m-song_bcg {
  width: 100%;
  height: 100%;
  background-image: url(http://s3.music.126.net/mobile-new/img/disc.png?d3bdd10%E2%80%A6=);
  background-size: cover;
  box-sizing: border-box;
  animation: xz 15s infinite linear;
}

.m_songpicurl img {
  height: 184px;
  box-sizing: border-box;
  width: 184px;
  border-radius: 50%;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  position: absolute;
}
.sprite1 {
  background: url(https://s1.ax1x.com/2020/03/28/GAbOgI.png);
  background-size: 100%;
  width: 45px;
  box-sizing: border-box;
  height: 45px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 999;
  transition: opacity 0.1s ease-in-out;
}
/* 旋转动画 */
@keyframes xz {
  form {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
/* 按钮组 */
.plate_btn {
  box-sizing: border-box;
  height: 45px;
  padding: 10px;
  width: 100%;
  display: flex;
  position: absolute;
  bottom: 10px;
  justify-content: space-around;
  align-items: center;
}
.plate_bottom {
  position: relative;
  z-index: 999;
  padding: 0 10px;
}
.slide_flex {
  display: flex;
  justify-content: space-around;
  align-items: center;
}
.plate_bottom span {
  color: #fff;
  font-size: 8px;
}
/* 滑块 */
.plate_slider {
  width: 70%;
  height: 2px;
  box-sizing: border-box;
  position: relative;
  display: flex;
  z-index: 999;
}

.custom-button {
  width: 10px;
  color: #fff;
  height: 10px;
  font-size: 10px;
  line-height: 18px;
  text-align: center;
  box-sizing: border-box;
  background-color: #fff;
  border-radius: 100px;
}
/* 功能按钮 */
.plate_gn {
  margin-top: 30px;
  height: 40px;
  box-sizing: border-box;
  display: flex;
  justify-content: space-around;
  position: relative;
  z-index: 999;
  align-items: center;
}
</style>


